.max-width {
  max-width: 1300px;
  padding: 0 80px;
  margin: 0;
}
.navbar {
  z-index: 1;
  position: relative;
  padding: 15px 0px;

  width: 100%;
  font-family: "Montserrat", sans-serif;
  /* background-color: #24252a; */
  background-color: #24252a;
  color: snow;
}

.class1 {
  padding: 15px 0;
  background-color: #24252a;
  color: snow;
}
.navbar .max-width {
  display: flex;
  justify-content: space-between;

  align-items: center;
}
.port {
  font-family: "Montserrat", "Gill Sans", "Gill Sans MT", "Trebuchet MS",
    sans-serif;
  color: snow;
  font-size: 30px;
  font-weight: 500px;
}
.port1 {
  color: snow;
}
.a-nav {
  display: block;
  font-family: "Montserrat", "Gill Sans", "Gill Sans MT", "Trebuchet MS",
    sans-serif;
  font-weight: 500px;
  font-size: 17px;
  /* color:#92451b; */
  color: snow;
  text-decoration: none;
}
.a-nav1 {
  color: snow;
}
.nav-link li {
  list-style-type: none;
  display: inline-block;
  padding: 0px 20px;
}
.nav-link li a {
  text-decoration: none;
  display: block;
  transition: all 0.7s ease 0s;
}
.nav-link li a:hover {
  /* color: #24252a; */
  color: #92451b;
}
/* style menu btn */
.menu-btn {
  font-size: 20px;
  font-size: 23px;
  cursor: pointer;
  display: none;
}
/* Home styling */
.home {
  display: flex;
  background: url("https://images.unsplash.com/photo-1518655048521-f130df041f66?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1750&q=80")
    no-repeat center;
  /* background-size: 100%; */
  height: 85vh;
  color: black;
  min-height: 500px;
  font-family: "Montserrat", "Gill Sans", "Gill Sans MT", "Trebuchet MS",
    sans-serif;
}
.home .max-width {
  margin: auto 0 auto 40px;
}
.home-content-1 {
  font-size: 30px;
}
.home-content-2 {
  font-size: 75px;
  font-weight: 600;
  margin-left: -4px;
}
.home-content-3 {
  font-size: 40px;
  margin: 5px 0;
}
.design1 {
  color: #92451b;
  font-weight: 500;
}
.design {
  color: #92451b;
}
/* About me section styling */
section {
  padding: 80px 0;
}
.aboutP {
  font-family: "Raleway-bold";
  font-size: 17px;
}

.about-me-section {
  font-family: Montserrat;
  /* margin-bottom: -20px; */
  background: #f8e4e1;
  height: 100vh;
}
.title1 {
  position: relative;
  text-align: center;
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 60px;
  padding-bottom: 20px;
}
.title1::before {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 50%;
  width: 205px;
  height: 4px;
  background: black;
  transform: translateX(-50%);
}
.title1::after {
  content: "who i am";
  position: absolute;
  bottom: -12px;
  left: 50%;
  font-size: 20px;
  color: #92451b;
  font-weight: 900;
  padding: 5px;
  background: #f8e4e1;

  transform: translateX(-50%);
}
.about-content,
.projects-content,
.skills .skills-content,
.contact .contact-content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.left {
  width: 45%;
}
.left img {
  height: 400px;
  width: 400px;
  object-fit: cover;

  border-radius: 6px;
}
.right {
  width: 55%;
}
.text {
  font-size: 25px;
  font-weight: 500;
  margin-bottom: 10px;
}
.text span {
  color: #92451b;
}
.right p {
  text-align: justify;
}
/* Projects section styling */
