  text-align: center;
  font-family: "Raleway-bold";
  font-size: 17px;
}
.bold {
  font-family: "Raleway-bold";
  font-size: 17px;
}
.popup-inner {
  background-color: black;
  position: relative;
  /* width:100%;
   height: 150px; */
  /* max-width: 500px; */
  height: 270px;
  max-height: 300px;
  /* width: calc(33% -20px); */

