button {
  height: 80%;
  width: 80%;
  border: 2px solid #92451b;
  background: #92451b;
  color: white;
  font-size: 15px;
  font-weight: 500;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
}
button:hover {
  color: #92451b;
  background: none;
}
.mouse {
  /* background-color:black; */
  position: relative;
  background-color: white;
  color: white;
  margin-top: -100px;
}

.mydivouter {
  position: relative;
  background: #f90;
  width: 200px;
  height: 120px;
  margin: 0 auto;
}
.mydivoverlap {
  position: relative;
  z-index: 1;
}
.mybuttonoverlap {
  position: absolute;
  z-index: 2;
  top: 44px;
  display: none;
  left: 59px;
}

.projects .card {
  height: 300px;
  max-height: 300px;
}
.projects .card p {
  text-align: justify;
}
.wrap {
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}
.projects,
.skills {
  font-family: Montserrat;
  background: #fff4f2;
}
