.projects .title1::after {
  background: #fff4f2;
  content: "My works";
}
.card {
  width: calc(33% - 20px);
  text-align: center;
  background: #f8e4e1;
  border-radius: 6px;
  padding: 25px 35px;
  margin-bottom: 20px;
  transition: all 0.3s ease;
  box-shadow: 0px 8px 8px -4px lightblue;
}
.card:hover {
  background: black;
  color: snow;
}
.box {
  /* position:relative; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
/* .box:hover .mybuttonoverlap{ 
	display:block;
} */
.card:hover .box {
  transform: scale(1.05);
}
.project-title {
  font-size: 25px;
  font-weight: 500;
  margin: 10px 0 7px 0;
}
.day-planner {
  height: 150px;
  width: 150px;
  object-fit: contain;
  border-radius: 50%;
  border: 5px solid #92451b;
  visibility: visible;
}
.jobs {
  height: 150px;
  width: 150px;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid #92451b;
}
.weather {
  height: 150px;
  width: 150px;
  object-fit: fill;
  border-radius: 50%;
  border: 5px solid #92451b;
}
.quiz {
  height: 150px;
  width: 150px;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid #92451b;
}
.mat {
  height: 150px;
  width: 150px;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid #92451b;
}
.blog {
  height: 150px;
  width: 150px;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid #92451b;
}
.box p {
  text-align: justify;
}
/* Skills section styling */
.skills {
