/* contact section styling */
.contact {
  background: #fff4f2;
}
.contact .title1::after {
  content: "Get in Touch";
  /* background:#fff4f2; */
}
.contact .contact-content p {
  font-family: Montserrat;
  font-size: 20px;
  text-align: justify;
}
.contact .contact-content .icons {
  margin: 10px 0;
}
.contact .contact-content .row {
  display: flex;
  height: 65px;
  align-items: center;
}
.contact .contact-content .row .info {
  margin-left: 20px;
}
.contact .contact-content .row i {
  font-size: 25px;
  color: #92451b;
}
.contact .contact-content .info .head {
  font-weight: 600;
}
.contact .contact-content .info .sub-title {
  color: black;
}
.contact .contact-content .column {
  width: calc(50% - 40px);
}
.contact .right {
  margin-top: -20px;
}
.contact .right form .fields {
  display: flex;
}
.contact .right form .field {
  height: 45px;
  width: 100%;
  margin-bottom: 15px;
}
.contact .right form .name {
  margin-right: 10px;
}
.contact .right form .email {
  margin-left: 10px;
}
.contact .right form .textarea {
  height: 80px;
}
.contact .right form .field input,
.contact .right form .textarea textarea {
  height: 100%;
  width: 100%;
  border: 1px solid lightgray;
  border-radius: 6px;
  outline: none;
  padding: 0 15px;
  font-size: 17px;
  font-family: Montserrat;
}
.contact .right form .textarea textarea {
  padding-top: 10px;
  /* resize: none; */
}
.contact .right form .button-area {
  height: 50px;
  width: 170px;
}
.contact .right form .button-area button {
  height: 80%;
  width: 80%;
  border: 2px solid #92451b;
  background: #92451b;
  color: white;
  font-size: 15px;
  font-weight: 500;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}
#contact {
  background: #f8e4e1;
}
.contact .right form .button-area button:hover {
  color: #92451b;
  background: none;
}
.contact a {
  text-decoration: none;
  color: black;
}

